import React from 'react';
import styles from './frame.css';
import classnames from 'classnames';

let sizeClasses = ['small', 'medium', 'big'];
let colorClasses = [styles.green, styles.orange, styles.teal];
let directions = ['normal', 'reverse'];

function Ball({
  color,
  size,
  positionX,
  positionY,
  opacity,
  duration,
  direction,
}) {
  let style = {
    top: `${positionY}px`,
    left: `${positionX}px`,
    opacity: opacity,
    animationDuration: `${duration}ms`,
    animationDirection: directions[direction],
  };
  return (
    <div
      style={style}
      className={classnames(
        styles.ball,
        sizeClasses[size],
        colorClasses[color],
      )}
    />
  );
}

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

function getAnimationDuration(max) {
  let min = 40;
  let maxAmount = Math.floor(max);
  return Math.floor(Math.random() * (maxAmount - min)) + min;
}

class AnimatedBackground extends React.Component {
  state: {
    width: number;
    height: number;
  };

  constructor(props) {
    super(props);
    this.state = {
      width: 0,
      height: 0,
    };
  }

  opacity = getRandomInt(100) / 100;

  componentDidMount() {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  }

  render() {
    if (!this.state.width && !this.state.height) {
      return null;
    }
    return (
      <Ball
        direction={getRandomInt(2)}
        duration={getAnimationDuration(200)}
        opacity={this.opacity}
        positionX={this.state.width}
        positionY={this.state.height}
        color={getRandomInt(3)}
        size={getRandomInt(3)}
      />
    );
  }
}

export default AnimatedBackground;
